
















import {
  Component, Vue, Prop, Emit,
} from 'vue-property-decorator';
import { ITicket } from '@/types/interface';

@Component
export default class TicketItemPanel extends Vue {
  @Prop() data!: ITicket;

  @Emit()
  select() {
    if (this.data.status === 'available') return this.data;
    return undefined;
  }
}
