































import { State, Action } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { ITicket } from '@/types/interface';
import banner from '../../components/Banner.vue';
import ticketItemPanel from './TicketItemPanel.vue';

@Component({
  components: {
    banner,
    ticketItemPanel,
  },
})

export default class MainPage extends Vue {
  @State('ticketList', { namespace: 'ticket' }) stateTicketList!: ITicket[] | [];
  @Action('getMemberCouponList', { namespace: 'ticket' }) getMemberCouponList!: () => void;
  @Action('getTicket', { namespace: 'ticket' }) getTicket!: (id: string) => Promise<void>;

  link() {
    this.$router.go(-1);
  }

  flag = 'available';

  get filterList() {
    return this.stateTicketList.filter((d: ITicket) => d.status === this.flag);
  }

  created() {
    this.getMemberCouponList();
  }

  select(data: ITicket | undefined) {
    if (data) {
      this.getTicket(data.linkId)
        .then(() => {
          this.$router.push({ path: '/ticket/content', query: { id: data.id } });
        });
    }
  }

  switchHandler(flag: string) {
    this.flag = flag;
  }
}
